@mixin flexColumnJcenterAcenter{
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

@mixin flexJcenter{
    display:flex;
    justify-content: center;
}

@mixin flexAcenter{
    display:flex;
    align-items: center;
}

@mixin flexJcenterAcenter{
    display:flex;
    justify-content: center;
    align-items: center;
}


@mixin flexJendAend{
    justify-content: flex-end;
    align-items: flex-end;
}